@import '@/scss/variables.scss';
@import '~vuetify/src/styles/styles.sass';




















































































































































.p-item {
  display: flex;
  padding-top: 34px;
}
.p-item__col-left {
  margin-right: 25px;
  flex-basis: 75px;
  flex-grow: 0;
  flex-shrink: 0;
}
.p-item__col-right {
  flex-grow: 1;
  flex-shrink: 0;
}
.p-item__title {
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 13px;
  margin-top: 10px;
}
.p-item__contacts {
  display: flex;
  font-size: 14px;
}
.p-item__contacts-links {
  display: flex;
  flex-direction: column;
  width: 296px;
}
.p-item__contacts-link {
  text-decoration: none;
}
.p-item__contacts-address-name {
  color: rgba(0, 0, 0, 0.38);
  min-width: 105px;
}
.p-item__form-wrapper {
  margin-top: 25px;
  max-width: 963px;
}
.p-item__form {
  padding: 12px 12px 0;
}
.p-item__form-title {
  margin-bottom: 9px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
}
.p-item__form-controls {}
.p-item__form-control {}

$_control-statuses-width: 271px;
.p-item__form-control--statuses {
  flex-basis: $_control-statuses-width;
  max-width: $_control-statuses-width;
  flex-grow: 0;
  flex-shrink: 0;
}
